import React from 'react';
import logo from './logo.svg';
import './App.css';

class Response {
	constructor(nextId, responseText) {
		// id to the next dialogue node
		this.nextId = nextId;
		this.responseText = responseText;
	}
}

class DialogueNode {
	constructor(id, text="...", img,  responses=null) {
		this.id = id;
		this.img = img;
		this.text = text;
		this.responses = responses;
	}
	
	// generate id based on the array of all dialogue nodes in the dialogue graph
	static generateId(array) {
		return array.length;
	}
}

const GameController = ({ text, img, responses, onClick, resetGame }) => {
	let responsesText = [];
	
	if(responses)
		responses.forEach((response, i) => {
			responsesText.push(<li
					key={i}
					nextid={response.nextId}
					style={{ listStyle: 'none', fontWeight: 'bold', cursor: 'pointer', margin: '10px auto' }}
					onClick={e => onClick(Number(e.target.getAttribute('nextid')))}
				>
				{response.responseText}
				</li>)
		})
	else responsesText.push(<li key={0} style={{ listStyle: 'none', fontWeight: 'bold', cursor: 'pointer', margin: '10px auto' }}>THE END!</li>)
	
	return (
		<div id="game-controller">
			<p>{text}</p>
			<img src={img} alt="" style={{ margin: '15px auto', width: '300px', height: '180px'  }} />
			<p>Your response:</p>
			<ul>
				{responsesText}
			</ul>
			<p style={{ margin: '30px auto', fontStyle: 'italic', cursor: 'pointer' }} onClick={resetGame}>Reset</p>
		</div>
	)
}

class App extends React.Component {
	constructor(props) {
		super(props);
		
		this.dialogueNodes = [];
		
		// 0
		this.dialogueNodes.push(
			new DialogueNode(DialogueNode.generateId(this.dialogueNodes), "*In the taxi* - Aomame : Take me to [that] adress. - Taxi Driver : Ok. Gotcha. Wanna change the music ?",
			"https://diez.md/wp-content/uploads/2016/09/TAXI-920x682.jpg",
			[
				new Response(1, "No. It's ok."),
				new Response(2, "Yes. What do you have ?")
			])
		);

		// 1
		this.dialogueNodes.push(
			new DialogueNode(DialogueNode.generateId(this.dialogueNodes), "... *Traffic Jam*",
			"https://img.kyodonews.net/english/public/images/posts/c7f15723cdcce4a8326f5d40ec2507e3/photo_l.jpg",
			[
				new Response(1, "Wait..."),
				new Response(3, "Exit Taxi")
			])
		);

		// 2
		this.dialogueNodes.push(
			new DialogueNode(DialogueNode.generateId(this.dialogueNodes), "Norvegian wood and ... We Will Rock You",
			"https://toppng.com/uploads/preview/car-salon-interior-black-control-panel-steering-wheel-11569966610kafezxfkrt.jpg",
			[
				new Response(1, "Beatles"),
				new Response(1, "Queen for the win!")
			])
		);
		
		// 3
		this.dialogueNodes.push(
			new DialogueNode(DialogueNode.generateId(this.dialogueNodes), "- Should I return ?",
			"data:image/jpeg;base64,",
			[
				new Response(1, "Yes"),
				new Response(4, "No")
			])
		)
		
		// 4
		this.dialogueNodes.push(
			new DialogueNode(DialogueNode.generateId(this.dialogueNodes), "Go downstairs under the highway. *Enter 1Q84*",
			"https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRGQoWOM6BiMO2qq24J-mtTu4zIZOKklYg4Hw&usqp=CAU",
			null)
		)
		
		this.state = {
			dialogueNode: this.dialogueNodes[0]
		}
		
		this.onResponseClick = this.onResponseClick.bind(this)
		this.resetGame = this.resetGame.bind(this)
	}
	
	onResponseClick(key) {
		this.setState({ dialogueNode: this.dialogueNodes[key] })
	}
	
	resetGame() {
		this.setState({ dialogueNode: this.dialogueNodes[0] })
	}
	
	render() {
		return (
			<div style={{ margin: '5% auto', textAlign: 'center' }}>
				<h1>Game Scenario Draft</h1>
				<GameController text={this.state.dialogueNode.text} img ={ this.state.dialogueNode.img } responses={this.state.dialogueNode.responses} onClick={this.onResponseClick} resetGame={this.resetGame} />
			</div>
		)
	}
}

export default App;
